<template>
  <a href="#" class="back-to-top"><i class="ri-arrow-up-line"></i></a>
</template>

<script>
import $ from 'jquery'
require('jquery.easing')

export default {
  name: "BackToTop",
  mounted() {
    $(window).scroll(function() {
      if ($(this).scrollTop() > 100) {
        $('.back-to-top').fadeIn('slow');
      } else {
        $('.back-to-top').fadeOut('slow');
      }
    });

    $('.back-to-top').click(function () {
      $('html, body').animate({
        scrollTop: 0
      }, 1500, 'easeInOutExpo');
      return false;
    });
  }
}
</script>

