import Vue from 'vue'
import VueRouter from 'vue-router'
import $ from 'jquery'
import store from '../store/index'
import Home from '../views/Home.vue'
// import About from "@/views/About";
import PlayGround from "@/views/PlayGround";
// import Services from "@/views/Services";
// import Portfolio from "@/views/Portfolio";
// import Team from "@/views/Team";
// import Contact from "@/views/Contact";
// import Features from "@/views/Features";
// import Cta from "@/views/Cta";
// import Pricing from "@/views/Pricing";
// import FAQ from "@/views/FAQ";
import NotFound from "@/views/NotFound";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/NotFound',
        name: 'NotFound',
        component: NotFound
    },
    // {
    //     path: '/about',
    //     name: 'About',
    //     component: About
    // },
    {
        path: '/playground',
        name: 'PlayGround',
        component: PlayGround
    },
    // {
    //     path: '/services',
    //     name: 'Services',
    //     component: Services
    // },
    // {
    //     path: '/portfolio',
    //     name: 'Portfolio',
    //     component: Portfolio
    // },
    // {
    //     path: '/team',
    //     name: 'Team',
    //     component: Team
    // },
    // {
    //     path: '/contact',
    //     name: 'Contact',
    //     component: Contact
    // },
    // {
    //     path: '/features',
    //     name: 'Features',
    //     component: Features
    // },
    // {
    //     path: '/cta',
    //     name: 'Cta',
    //     component: Cta
    // },
    // {
    //     path: '/pricing',
    //     name: 'Pricing',
    //     component: Pricing
    // },
    // {
    //     path: '/faq',
    //     name: 'FAQ',
    //     component: FAQ
    // },
    // region PlayGround
    {
        path: '/LinearRegression',
        name: 'LinearRegression',
        component: () => import('@/views/playGround/LinearRegression')
    },
    {
        path: '/LogisticRegression',
        name: 'LogisticRegression',
        component: () => import('@/views/playGround/LogisticRegression')
    },
    {
        path: '/BinaryClassification',
        name: 'BinaryClassification',
        component: () => import('@/views/playGround/BinaryClassification')
    },
    {
        path: '/MNISTClassification',
        name: 'MNISTClassification',
        component: () => import('@/views/playGround/MNISTClassification/MNISTClassification')
    },
    // endregion
    // region Services
    // {
    //     path: '/Colorizer',
    //     name: 'Colorizer',
    //     component: () => import('@/views/services/Colorizer')
    // },
    // {
    //     path: '/StyleTransfer',
    //     name: 'StyleTransfer',
    //     component: () => import('@/views/services/StyleTransfer/StyleTransfer')
    // },
    // endregion
    {
        path: '*',
        redirect: {name: 'NotFound'}
    },
    {
        path: '/Test',
        name: 'Test',
        component: () => import('@/views/Test')
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.afterEach(() => {
    store.state.appSections.Footer = router.currentRoute.name !== 'Home';

    $('html, body').animate({scrollTop: 0}, 'easeInOutExpo');
})

export default router
