<template>
  <div class="ds-parent-btn">
    <button :disabled="disabled" @click="click" class="ds-btn">{{ text }}</button>
  </div>
</template>

<script>
export default {
  name: "Button",
  props: {
    text: {
      type: String,
      required: true
    },
    click: {
      type: Function,
      required: true
    },
    disabled: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style>

.ds-parent-btn {
  text-align: center;
}

.ds-btn {
  background: #fff;
  font-family: "Nunito", sans-serif;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 12px 32px;
  border-radius: 50px;
  transition: 0.3s;
  line-height: 1;
  color: #e43c5c;
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
  margin: 6px;
  border: 2px solid #e43c5c;
}

.ds-btn:hover {
  background: #e43c5c;
  color: #fff;
  text-decoration: none;
}

.ds-btn:focus {
  outline: none;
}

.ds-btn:disabled {
  background-color: #fdeff2;
  color: #e43c5c;
  opacity: 0.65;
  cursor: not-allowed;
}
</style>