<template>
  <!--    <header id="header" class="fixed-top ">-->
  <header id="header" :class="headerClass">
    <div class="container d-flex align-items-center">

      <!--<h1 class="logo mr-auto"><a href="index.html">Tempo</a></h1>-->
      <h1 class="logo mr-auto">
        <router-link :to="{ name: 'Home' }">{{ $store.state.appName }}</router-link>
      </h1>
      <!-- Uncomment below if you prefer to use an image logo -->
      <!--<a href="index.html" class="logo mr-auto"><img src="assets/img/logo.png" alt="" class="img-fluid"></a>-->

      <nav class="nav-menu d-none d-lg-block">
        <ul>
          <!--<li class="active"><a href="index.html">Home</a></li>-->
          <li :class="{ active: navs[0].status }">
            <router-link :to="{ name: 'Home' }">Home</router-link></li>

          <li v-if="$store.state.appSections.About" :class="{ active: navs[1].status }">
            <router-link :to="{ name: 'About' }">About</router-link></li>

          <li v-if="$store.state.appSections.PlayGround" :class="{ active: navs[2].status }">
            <router-link :to="{ name: 'PlayGround' }">PlayGround</router-link></li>

          <li v-if="$store.state.appSections.Services" :class="{ active: navs[3].status }">
            <router-link :to="{ name: 'Services' }">Services</router-link></li>

          <li v-if="$store.state.appSections.Portfolio" :class="{ active: navs[4].status }">
            <router-link :to="{ name: 'Portfolio' }">Portfolio</router-link></li>

          <li v-if="$store.state.appSections.Team" :class="{ active: navs[5].status }">
            <router-link :to="{ name: 'Team' }">Team</router-link></li>

          <li v-if="$store.state.appSections.Contact" :class="{ active: navs[6].status }">
            <router-link :to="{ name: 'Contact' }">Contact</router-link></li>

          <!--TODO API, Jobs, How to use, Docs, Register, Login, Licenses, Privacy Policy, -->
          <li v-if="$store.state.appSections.DropDown" class="drop-down"><a></a>
            <ul>
              <li v-if="$store.state.appSections.Features" :class="{ active: navs[7].status }">
                <router-link :to="{ name: 'Features' }">Features</router-link></li>

              <li v-if="$store.state.appSections.Cta" :class="{ active: navs[8].status }">
                <router-link :to="{ name: 'Cta' }">Cta</router-link></li>

              <li v-if="$store.state.appSections.Pricing" :class="{ active: navs[9].status }">
                <router-link :to="{ name: 'Pricing' }">Pricing</router-link></li>

              <li v-if="$store.state.appSections.FAQ" :class="{ active: navs[10].status }">
                <router-link :to="{ name: 'FAQ' }">FAQ</router-link></li>
<!--              <li class="drop-down"><a href="#">Deep Drop Down</a>-->
<!--                <ul>-->
<!--                  <li><a href="#">Deep Drop Down 1</a></li>-->
<!--                  <li><a href="#">Deep Drop Down 2</a></li>-->
<!--                  <li><a href="#">Deep Drop Down 3</a></li>-->
<!--                  <li><a href="#">Deep Drop Down 4</a></li>-->
<!--                  <li><a href="#">Deep Drop Down 5</a></li>-->
<!--                </ul>-->
<!--              </li>-->
            </ul>
          </li>

        </ul>
      </nav><!-- .nav-menu -->

    </div>
  </header><!-- End Header -->
</template>

<script>
export default {
  name: "Header",
  props: {
    innerPage: {
      type: Boolean,
      default: false
    },
    nav: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      navs:[
        {name: 'Home', status: false},
        {name: 'About', status: false},
        {name: 'PlayGround', status: false},
        {name: 'Services', status: false},
        {name: 'Portfolio', status: false},
        {name: 'Team', status: false},
        {name: 'Contact', status: false},
        {name: 'Features', status: false},
        {name: 'Cta', status: false},
        {name: 'Pricing', status: false},
        {name: 'FAQ', status: false}
      ]
    }
  },
  methods: {
    activateNav (nav) {
      this.navs.forEach(element => {
        element.status = nav === element.name;
      })
    }
  },
  computed:{
    headerClass () {
      return this.innerPage ? 'fixed-top header-inner-pages' : 'fixed-top'
    },
  },
  mounted() {
    this.activateNav(this.nav)
  }
}
</script>

